.swiper-container {
    width: 100%;
    height: 100%;
  }
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
  
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;  
    cursor: grab;
  
    &:active{
      cursor: grabbing;
    }
  }
  .swiper-container-v {
    background: #eee;
    transition-property: transform; 
    transition-duration: 1s;

  
    .swiper-wrapper{
      .swiper-slide{
        &:nth-child(1){
          background: rgb(255,0,0);
          background: linear-gradient(180deg, rgba(255,0,0,0.5) 0%, rgba(255,108,0,0.5) 100%);
        }
        &:nth-child(2){
          background: rgb(255,158,0);
          background: linear-gradient(180deg, rgba(255,108,0,0.5) 0%, rgba(255,248,0,0.5) 100%);
        }
        &:nth-child(3){
          background: rgb(255,248,0);
          background: linear-gradient(180deg, rgba(255,248,0,0.5) 0%, rgba(0,255,76,0.5) 100%);
        }
        &:nth-child(4){
          background: rgb(0,255,76);
          background: linear-gradient(180deg, rgba(0,255,76,0.5) 0%, rgba(0,155,255,0.5) 100%);
        }
        &:nth-child(5){
          background: rgb(0,155,255);
          background: linear-gradient(180deg, rgba(0,155,255,0.5) 0%, rgba(235,0,255,0.5) 100%);
        }
      }
    }
  }
  
  .swiper-slide h1{
      z-index: 1;
      color: #fff;
  }
  .swiper-slide img{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    height: 100%;
    object-fit: cover; 
  } 
  .swiper-slide .title{
    z-index: 1;
    color: #000;
    background-color: #fff;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 50px 15px;
  }
  
  .swiper-slide .photoandart{
    height: calc(100% - 6vw);
    width: 100%;
    background: rgba(255, 255, 255, 0.8);
    border-radius: 3vw;
    margin: 3vw;
    display: flex;
    flex-direction: row;
    align-content: center;
    position: relative;
  
    @media (max-width: 767px){
      flex-direction: column;
    }
  
    &-l, &-r{
      width: 50%;
      margin: 3vw;
      display: flex;
      justify-content: flex-end;
      flex-direction: column;
  
      @media (max-width: 767px){
        width: calc(100% - 6vw);
      }
      img{
        position: unset;
        width: 100%;
        height: auto;
        object-fit: contain;
        border-radius: 1vw;
      }
      p{
        text-align: justify;
        max-height: 710px;
        overflow: hidden;
        margin: 1rem;
  
        @media (max-width: 767px){
          font-size: 14px;
          margin: 1rem;
          max-height: 420px;
        }
      }
    }
  
    &-l{
      @media (max-width: 767px){
        margin-bottom: 0;
      }      
    }
    &-r{
      @media (max-width: 767px){
        margin-top: 0;
      }      
    }
  }  
  
    .swiper-container-horizontal>.swiper-pagination-bullets{
      width: auto;
      left: 10px;
      right: unset;
      top: 16px;
      bottom: unset;
      margin: 0;
  
      .swiper-pagination-bullet-active{
        background-color: #000;
        opacity: .2;
      }
    }
  
    .swiper-container-vertical>.swiper-pagination-bullets{
      transform: none;
      top: 11px;
      left: 14px;
      margin: 0;
      width: fit-content;
    }
  
    .swiper-pagination-bullet{
      width: 16px;
      height: 16px;
    }
  
    .swiper-container-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet{
      margin: 0 $horizontal-margin;
      transition: all 0.2s ease-in;
    }
    .swiper-container-vertical>.swiper-pagination-bullets .swiper-pagination-bullet{
      margin: $vertical-margin 0;
      transition: all 0.2s ease-in;
    }  
    #navigation{
      position: fixed;
      top: 10px;
      left: 10px;
      z-index: 2;
      .navigation_row{
        float: left;
        margin: 0 4px;
        .navigation_btn{
          margin: 6px 0;
          transition: all 0.2s ease-in;
          width: 16px;
          height: 16px;
          border-radius: 100%;
          background: #000;
          opacity: .2;
          cursor: pointer;
  
          &:hover{
            opacity: .5;
          }
        }
      }
    }